<template>
  <view class="admin-container">
    <view class="page-header">
      <text class="page-title">ÂêàÂùõÊ≥ï‰ºö‰ªãÁªçÁÆ°ÁêÜ</text>
      <text class="page-desc">ÁÆ°ÁêÜÂ∞èÁ®ãÂ∫èÂêàÂùõÊ≥ï‰ºöÈ°µÈù¢ÁöÑ‰ªãÁªçÂÜÖÂÆπ</text>
      <button class="add-btn" @click="showAddModal">+ Êñ∞Â¢û‰ªãÁªç</button>
    </view>
    
    <!-- Ê∑ªÂä†/ÁºñËæëË°®ÂçïÁõ¥Êé•Âú®È°µÈù¢ÊòæÁ§∫ -->
    <view v-if="isEdit || editIndex === -1" class="intro-edit-form">
      <view class="form-content">
        <view class="form-item">
          <text class="form-label">‰ªãÁªçÂÜÖÂÆπ *</text>
          <textarea v-model="currentIntro.content" class="form-textarea" placeholder="ËØ∑ËæìÂÖ•ÂêàÂùõÊ≥ï‰ºö‰ªãÁªçÂÜÖÂÆπ" maxlength="500" />
          <text class="char-count">{{ currentIntro.content.length }}/500</text>
        </view>
        <view class="form-item">
          <text class="form-label">ÊéíÂ∫è</text>
          <input v-model="currentIntro.order" type="number" class="form-input" placeholder="ËØ∑ËæìÂÖ•ÊéíÂ∫èÂè∑" />
        </view>
        <view class="form-item">
          <text class="form-label">ÊñáÂ≠óÈ¢úËâ≤</text>
          <view class="color-picker">
            <view class="color-option" v-for="option in textColorOptions" :key="option.key" 
                  :style="{ backgroundColor: option.color }" 
                  :class="{ active: currentIntro.textColor === option.color }"
                  @click="selectTextColor(option.color)">
            </view>
            <input v-model="currentIntro.textColor" class="color-input" placeholder="#333333" />
          </view>
        </view>
        <view class="form-item">
          <text class="form-label">ËÉåÊôØÈ¢úËâ≤</text>
          <view class="color-picker">
            <view class="color-option" v-for="option in bgColorOptions" :key="option.key" 
                  :style="{ backgroundColor: option.color }" 
                  :class="{ active: currentIntro.bgColor === option.color }"
                  @click="selectBgColor(option.color)">
            </view>
            <input v-model="currentIntro.bgColor" class="color-input" placeholder="#FFFFFF" />
          </view>
        </view>
        <view class="form-item">
          <text class="form-label">ËÉåÊôØÂõæÁâá</text>
          <view class="image-upload" @click="chooseImage">
            <image v-if="currentIntro.bgImage" :src="currentIntro.bgImage" class="upload-preview" mode="aspectFill" />
            <view v-else class="upload-placeholder">
              <text class="upload-icon">üñºÔ∏è</text>
              <text class="upload-text">ÁÇπÂáªÈÄâÊã©ËÉåÊôØÂõæÁâáÔºàÂèØÈÄâÔºâ</text>
            </view>
          </view>
        </view>
        <view class="form-item">
          <text class="form-label">Áä∂ÊÄÅ</text>
          <switch :checked="currentIntro.enabled" @change="onSwitchChange" color="#667eea" />
          <text class="switch-label">{{ currentIntro.enabled ? 'ÂêØÁî®' : 'Á¶ÅÁî®' }}</text>
        </view>
      </view>
      <view class="form-actions">
        <button class="cancel-btn" @click="cancelEdit">ÂèñÊ∂à</button>
        <button class="save-btn" @click="saveIntro">‰øùÂ≠ò</button>
      </view>
    </view>
    <!-- ‰ªãÁªçÂàóË°® -->
    <view class="intros-list" v-if="!isEdit && editIndex === -1">
      <view v-for="(intro, index) in intros" :key="intro._id" class="intro-card">
        <view class="intro-content">
          <text class="intro-text">{{ intro.content }}</text>
          <view class="intro-preview" :style="{ backgroundColor: intro.bgColor }">
            <text class="preview-text" :style="{ color: intro.textColor }">{{ intro.content }}</text>
          </view>
        </view>
        <view class="intro-info">
          <view class="info-row">
            <text class="info-label">ÊéíÂ∫èÔºö</text>
            <text class="info-value">{{ intro.order || 0 }}</text>
          </view>
          <view class="info-row">
            <text class="info-label">Áä∂ÊÄÅÔºö</text>
            <text class="info-value" :class="intro.enabled ? 'enabled' : 'disabled'">
              {{ intro.enabled ? 'ÂêØÁî®' : 'Á¶ÅÁî®' }}
            </text>
          </view>
          <view class="info-row">
            <text class="info-label">ÊñáÂ≠óÈ¢úËâ≤Ôºö</text>
            <view class="color-preview" :style="{ backgroundColor: intro.textColor }"></view>
          </view>
          <view class="info-row">
            <text class="info-label">ËÉåÊôØÈ¢úËâ≤Ôºö</text>
            <view class="color-preview" :style="{ backgroundColor: intro.bgColor }"></view>
          </view>
        </view>
        <view class="intro-actions">
          <button class="action-btn edit" @click="editIntro(index)">ÁºñËæë</button>
          <button class="action-btn delete" @click="deleteIntro(intro._id)">Âà†Èô§</button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue'

// ÂØºÂÖ•‰∫ëÂØπË±°
const jointManagement = uniCloud.importObject('joint-management')

export default {
  components: { uniPopup },
  
  data() {
    return {
      intros: [],
      isEdit: false,
      editIndex: -1,
      textColors: ['#333333', '#666666', '#999999', '#FFFFFF', '#000000', '#667eea', '#ff6b35'],
      bgColors: ['#FFFFFF', '#F8F9FA', '#E9ECEF', '#DEE2E6', '#667eea', '#f0f4ff', '#fff7ec'],
      currentIntro: {
        _id: '',
        content: '',
        order: 0,
        textColor: '#333333',
        bgColor: '#FFFFFF',
        bgImage: '',
        enabled: true
      }
    }
  },
  
  onLoad() {
    this.loadIntros()
  },
  
  computed: {
    textColorOptions() {
      return this.textColors.map((color, index) => ({
        color,
        index,
        key: `text-${index}`
      }))
    },
    bgColorOptions() {
      return this.bgColors.map((color, index) => ({
        color,
        index,
        key: `bg-${index}`
      }))
    }
  },
  
  methods: {
    async loadIntros() {
      try {
        const result = await jointManagement.getIntros()
        if (result.success) {
          this.intros = result.data || []
        } else {
          uni.showToast({ title: result.message, icon: 'none' })
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂêàÂùõÊ≥ï‰ºö‰ªãÁªçÂ§±Ë¥•:', error)
        uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    showAddModal() {
      this.isEdit = true
      this.editIndex = -1
      this.currentIntro = {
        _id: '',
        content: '',
        order: 0,
        textColor: '#333333',
        bgColor: '#FFFFFF',
        bgImage: '',
        enabled: true
      }
    },
    
    editIntro(index) {
      this.isEdit = true
      this.editIndex = index
      this.currentIntro = JSON.parse(JSON.stringify(this.intros[index]))
    },
    
    cancelEdit() {
      this.isEdit = false
      this.editIndex = -1
      this.currentIntro = {
        _id: '',
        content: '',
        order: 0,
        textColor: '#333333',
        bgColor: '#FFFFFF',
        bgImage: '',
        enabled: true
      }
    },
    
    selectTextColor(color) {
      this.currentIntro.textColor = color
    },
    
    selectBgColor(color) {
      this.currentIntro.bgColor = color
    },
    
    chooseImage() {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: (res) => {
          this.currentIntro.bgImage = res.tempFilePaths[0]
        }
      })
    },
    
    onSwitchChange(e) {
      this.currentIntro.enabled = e.detail.value
    },
    
    async saveIntro() {
      if (!this.currentIntro.content.trim()) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•‰ªãÁªçÂÜÖÂÆπ', icon: 'none' })
        return
      }
      
      try {
        const data = {
          ...this.currentIntro,
          order: Number(this.currentIntro.order) || 0
        }
        
        if (this.isEdit && data._id) {
          const updateData = { ...data }
          delete updateData._id
          const result = await jointManagement.updateIntro({ _id: data._id, ...updateData })
          if (!result.success) {
            throw new Error(result.message || 'Êõ¥Êñ∞Â§±Ë¥•')
          }
        } else {
          const result = await jointManagement.addIntro(data)
          if (!result.success) {
            throw new Error(result.message || 'Ê∑ªÂä†Â§±Ë¥•')
          }
        }
        
        this.cancelEdit()
        await this.loadIntros()
        uni.showToast({ title: this.isEdit ? 'Êõ¥Êñ∞ÊàêÂäü' : 'Ê∑ªÂä†ÊàêÂäü' })
      } catch (error) {
        console.error('‰øùÂ≠òÂêàÂùõÊ≥ï‰ºö‰ªãÁªçÂ§±Ë¥•:', error)
        uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    async deleteIntro(id) {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™‰ªãÁªçÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              const result = await jointManagement.deleteIntro({ _id: id })
              if (result.success) {
                await this.loadIntros()
                uni.showToast({ title: 'Âà†Èô§ÊàêÂäü', icon: 'success' })
              } else {
                uni.showToast({ title: result.message, icon: 'none' })
              }
            } catch (error) {
              console.error('Âà†Èô§ÂêàÂùõÊ≥ï‰ºö‰ªãÁªçÂ§±Ë¥•:', error)
              uni.showToast({ title: 'Âà†Èô§Â§±Ë¥•', icon: 'none' })
            }
          }
        }
      })
    }
  }
}
</script>

